import * as THREE from 'three'
import AssetManager from './AssetManager'
import Texture from './Texture'
import Config from './Config'
import Utils from './Utils'
import LevelProgressBar from './LevelProgressBar'
import { Text } from 'troika-three-text'

class SSScreen {
    constructor(jsondata, loadingManager, scene, camera, renderer, sound, backgroundSound, callback, sdk) {
        this.callback = callback;
        this.jsondata = jsondata;

        this.imagePaths = [];
        for (let i = 0; i < jsondata["images"].length; ++i) {
            this.imagePaths.push(jsondata["images"][i]["image_path"]);
        }

        this.buttonimages = {};
        for (let i = 0; i < jsondata["buttons"].length; ++i) {
            let button = jsondata["buttons"][i];
            this.buttonimages[button["id"]] = {}

            this.imagePaths.push(button["normal_image_path"]);
            this.buttonimages[button["id"]]["normal_image_path"] = this.imagePaths.length - 1;

            if (button["pressed_image_path"]) {
                this.imagePaths.push(button["pressed_image_path"]);
                this.buttonimages[button["id"]]["pressed_image_path"] = this.imagePaths.length - 1;
            }
            if (button["inactive_image_path"]) {
                this.imagePaths.push(button["inactive_image_path"]);
                this.buttonimages[button["id"]]["inactive_image_path"] = this.imagePaths.length - 1;
            }
            if (button["state1_image_path"]) {
                this.imagePaths.push(button["state1_image_path"]);
                this.buttonimages[button["id"]]["state1_image_path"] = this.imagePaths.length - 1;
            }
            if (button["state2_image_path"]) {
                this.imagePaths.push(button["state2_image_path"]);
                this.buttonimages[button["id"]]["state2_image_path"] = this.imagePaths.length - 1;
            }
        }

        console.log(this.imagePaths);

        this.sdk = sdk;

        console.log(this.sdk);
        console.log(sdk);

        this.imageAssets = [];

        this.showAudioReplay = false;
        this.camera = camera;
        this.scene = scene;
        this.renderer = renderer;
        this.sound = sound;
        this.backgroundSound = backgroundSound;

        this.enableEvents = true;

        this.assetManager = new AssetManager();
        this.raycaster = new THREE.Raycaster();
        this.mouse = new THREE.Vector2();
        this.load(loadingManager);

        this.positionalObjs = [];
        this.buttons = [];
    }

    dispose() {
        // Dispose only what is created here
        this.assetManager.dispose();

        if (this.rewardText) {
            this.scene.remove(this.rewardText);
            this.rewardText.dispose();
        }

        document.removeEventListener('mousedown', this.onDocumentMouseDown, false);
        document.removeEventListener('touchdown', this.onDocumentMouseDown, false);

        for (let i = 0; i < this.imageAssets.length; ++i) {
            this.scene.remove(this.imageAssets[i]);
        }

        if (this.level1ProgressBar) this.scene.remove(this.level1ProgressBar.obj);
        if (this.level2ProgressBar) this.scene.remove(this.level2ProgressBar.obj);
        if (this.level3ProgressBar) this.scene.remove(this.level3ProgressBar.obj);
        if (this.level4ProgressBar) this.scene.remove(this.level4ProgressBar.obj);
    }

    load = async (loadingManager) => {
        console.log("MenuBar loading...");

        // Load all assets. Textures, sounds, models required
        await this.assetManager.loadTextures(loadingManager, this.imagePaths);

        // Create scene
        this.createScene();

        loadingManager.onSceneLoaded();
    }

    updateForResize = () => {

        for (let i = 0; i < this.positionalObjs.length; ++i) {
            this.positionalObjs[i].updateForResize();
        }

        if (this.rewardText) {
            this.rewardText.position.x = this.rewardButton.position.x + 20;
            this.rewardText.sync()
        }
    }

    getTexture = (image, name, transparent, x, y, z, vec = null) => {
        var asset = Utils.getTexture(image, name, transparent, x, y, z, vec);
        this.imageAssets.push(asset);
        return asset;
    }

    getTexture2 = (image, name, transparent, xref, x, yref, y, z, vec = null, wrappingS = null, wrappingT = null) => {
        let texture = this.getTexture(image, name, transparent, x, y, z, vec, wrappingS, wrappingT);
        return new Texture(texture, xref, x, yref, y);
    }

    createScene = () => {
        // Add Game Objects.
        for (let i = 0; i < this.jsondata["images"].length; ++i) {
            let obj = this.jsondata["images"][i];
            let position = obj["position"];
            let scale = obj["scale"];
            let image = this.getTexture2(this.assetManager.textures[i],
                obj["id"], true, obj["xref"], position.x, obj["yref"], position.y, position.z);
            image.setScale(scale.x, scale.y);

            this.scene.add(image.texture);

            this.positionalObjs.push(image);

            console.log("Addded a image to scene");
        }

        console.log(this.buttonimages);
        for (let i = 0; i < this.jsondata["buttons"].length; ++i) {
            let obj = this.jsondata["buttons"][i];
            let position = obj["position"];
            let scale = obj["scale"];
            let images = this.buttonimages[obj["id"]]
            console.log(obj["id"]);
            console.log(images);

            let normal_image = null;
            normal_image = this.getTexture2(this.assetManager.textures[images["normal_image_path"]],
                obj["id"], true, obj["xref"], position.x, obj["yref"], position.y, position.z);
            normal_image.setScale(scale.x, scale.y);

            let pressed_image = null;
            if (images["normal_image_path"]) {
                pressed_image = this.getTexture2(this.assetManager.textures[images["normal_image_path"]],
                    obj["id"], true, obj["xref"], position.x, obj["yref"], position.y, position.z);
                pressed_image.setScale(scale.x, scale.y);
            }

            let inactive_image = null;
            if (images["inactive_image_path"]) {
                inactive_image = this.getTexture2(this.assetManager.textures[images["inactive_image_path"]],
                    obj["id"], true, obj["xref"], position.x, obj["yref"], position.y, position.z);
                inactive_image.setScale(scale.x, scale.y);
            }

            let state1_image = null;
            if (images["state1_image_path"]) {
                state1_image = this.getTexture2(this.assetManager.textures[images["state1_image_path"]],
                    obj["id"], true, obj["xref"], position.x, obj["yref"], position.y, position.z);
                state1_image.setScale(scale.x, scale.y);
            }

            let state2_image = null;
            if (images["state2_image_path"]) {
                state2_image = this.getTexture2(this.assetManager.textures[images["state2_image_path"]],
                    obj["id"], true, obj["xref"], position.x, obj["yref"], position.y, position.z);
                state2_image.setScale(scale.x, scale.y);
            }

            if (normal_image) this.positionalObjs.push(normal_image);
            if (pressed_image) this.positionalObjs.push(pressed_image);
            if (inactive_image) this.positionalObjs.push(inactive_image);
            if (state1_image) this.positionalObjs.push(state1_image);
            if (state2_image) this.positionalObjs.push(state2_image);

            let state = "NORMAL";
            let activeimage = normal_image;
            if (obj["active"] == 0) {
                state = "INACTIVE";
                activeimage = inactive_image;
            }
            else if (obj["toggle"] == 1) {
                if (obj["action"] == "music_setting") {
                    let volume = "0";
                    if (localStorage.getItem("volume"))
                        volume = localStorage.getItem("volume")
                    console.log("volume: ", volume);
                    if (volume == "1") {
                        this.setVolumeSetting(true);
                        state = "STATE1";
                        activeimage = state1_image;
                    }
                    else {
                        this.setVolumeSetting(false);
                        state = "STATE2";
                        activeimage = state2_image;
                    }
                }
                else {
                    state = "STATE1";
                    activeimage = state1_image;
                }
            }

            this.scene.add(activeimage.texture);
            this.buttons.push({
                "normal_image": normal_image, "pressed_image": pressed_image,
                "inactive_image": inactive_image, "state1_image": state1_image, "state2_image": state2_image,
                "state": state, "id": obj["id"]
            });
        }

        this.updateForResize();

        document.addEventListener('mousedown', this.onDocumentMouseDown, false);
        document.addEventListener('touchdown', this.onDocumentMouseDown, false);

        this.callback.onScreenLoaded(this);
    }

    setEnableEvents = (bEnable) => {
        this.enableEvents = bEnable;
    }

    update = () => {

    }

    updateRewardText = (rewardNum) => {
        this.rewardText.text = '' + rewardNum;

        // Update the rendering:
        this.rewardText.sync()
    }

    enableAudioPlayButton = (bEnable) => {
        this.scene.remove(this.audioReplayButton);
        this.showAudioReplay = bEnable;
        if (bEnable) {
            this.scene.add(this.audioReplayButton);
        }
    }

    enableReward = (bEnable) => {
        if (bEnable) {
            this.scene.add(this.rewardButton);
            this.scene.remove(this.rewardInactiveButton);
        }
        else {
            this.scene.remove(this.rewardButton);
            this.scene.add(this.rewardInactiveButton);
        }
    }

    enableMusic = (bEnable) => {
        this.scene.remove(this.musicButton);
        this.scene.remove(this.musicInactiveButton);
        if (bEnable) {
            this.scene.add(this.musicButton);
        }
        else {
            this.scene.add(this.musicInactiveButton);
        }

        console.log("----")
        console.log(this.sdk);
        if (this.sdk)
            this.sdk.setMusicButtonClicked(bEnable);
    }

    enableHint = (bEnable) => {
        if (bEnable) {
            this.scene.add(this.hintButton);
            this.scene.remove(this.hintInactiveButton);
        }
        else {
            this.scene.remove(this.hintButton);
            this.scene.add(this.hintInactiveButton);
        }
    }

    enableHome = (bEnable) => {
        if (bEnable) {
            this.scene.add(this.homeButton);
            this.scene.remove(this.homeInactiveButton);
        }
        else {
            this.scene.remove(this.homeButton);
            this.scene.add(this.homeInactiveButton);
        }
    }

    enableBack = (bEnable) => {
        if (bEnable) {
            this.scene.add(this.backButton);
            this.scene.remove(this.backInactiveButton);
        }
        else {
            this.scene.remove(this.backButton);
            this.scene.add(this.backInactiveButton);
        }
    }

    enableSkip = (bEnable) => {
        this.scene.remove(this.skipButton);
        if (bEnable) {
            this.scene.add(this.skipButton);
        }
    }

    setVolumeSetting = (value) => {
        localStorage.setItem("volume", value ? "1" : "0");
        this.backgroundSound.setVolume(value ? 0.5 : 0);
    }

    onDocumentMouseDown = (event) => {
        if (!this.enableEvents)
            return;

        this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = - (event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.mouse, this.camera);

        var intersects = this.raycaster.intersectObjects(this.scene.children);

        if (intersects.length > 0) {
            let name = intersects[0].object.name;
            for (let i = 0; i < this.jsondata["buttons"].length; ++i) {
                let obj = this.jsondata["buttons"][i];
                if (obj["id"] == name) {
                    console.log("Its " + obj["id"]);
                    for (let k = 0; k < this.buttons.length; ++k) {
                        if (this.buttons[k]["id"] == name) {
                            if (this.buttons[k]["state"] == "INACTIVE") {
                                break;
                            }
                            if (obj["toggle"] == 1) {
                                if (this.buttons[k]["state"] == "STATE2") {
                                    this.scene.remove(this.buttons[k]["state2_image"].texture);
                                    this.scene.add(this.buttons[k]["state1_image"].texture);
                                    this.buttons[k]["state"] = "STATE1";
                                }
                                else if (this.buttons[k]["state"] == "STATE1") {
                                    this.scene.remove(this.buttons[k]["state1_image"].texture);
                                    this.scene.add(this.buttons[k]["state2_image"].texture);
                                    this.buttons[k]["state"] = "STATE2";
                                }

                                if (obj["action"] == "music_setting") {
                                    this.setVolumeSetting(this.buttons[k]["state"] == "STATE1");
                                }
                            }
                            else {
                                if (obj["action"] == "home") {
                                    console.log("Home Action");
                                }
                                else if (obj["action"] == "hint") {
                                    console.log("Hint Action");
                                }
                                else if (obj["action"] == "back") {
                                    console.log("Back Action");
                                }
                            }

                            break;
                        }
                    }
                    break;
                }
            }
        }
    }
}

export default SSScreen 