let audioNum = 0;

class SAudio {
    constructor(callback) {
        this.uuid = audioNum;
        audioNum += 1;
        this.callback = callback;        
        this.isPlaying = false;
        this.audio = null;
    }

    dispose() {
        this.stop();
        this.audio.removeEventListener('ended', this.audioPlayEnded, false);
    }

    audioPlayEnded = () => {
        this.isPlaying = false;
        this.callback.audioPlaybackEnded(this);
    }

    play = (soundfile) => {        

        this.audio = new Audio(soundfile);
        this.audio.play();
        this.isPlaying = true;
        this.audio.addEventListener('ended', this.audioPlayEnded, false);
    }

    pause = (val) => {
        if(this.isPlaying) {
            if(val)
                this.audio.pause();
            else
                this.audio.play();
        }
    }

    stop = () => {        
        if(this.isPlaying)
            this.audio.pause();
    }
}

export default SAudio