import * as THREE from 'three'
import LoadingManager from './LoadingManager';
import Config from './Config.js'
import SDK from './sdk/SDK';
import SSScene from './SSScene';
import scene0json from './scene0.json';
import scene1json from './scene1.json';
import scene2json from './scene2.json';
import scene3json from './scene3.json';
import scene4json from './scene4.json';

var hidden, visibilityChange;
if (typeof document.hidden !== "undefined") { // Opera 12.10 and Firefox 18 and later support
    hidden = "hidden";
    visibilityChange = "visibilitychange";
} else if (typeof document.msHidden !== "undefined") {
    hidden = "msHidden";
    visibilityChange = "msvisibilitychange";
} else if (typeof document.webkitHidden !== "undefined") {
    hidden = "webkitHidden";
    visibilityChange = "webkitvisibilitychange";
}

class Game {
    constructor(container) {
        window.addEventListener('resize', () => {
            this.updateScale();
        });

        var vol = localStorage.getItem("volume");
        if (vol == null)
            localStorage.setItem("volume", "1");

        // Scene
        this.scene = new THREE.Scene()

        this.loadingManager = new LoadingManager(this.scene);

        this.container = container;

        // Camera
        var aspect = window.innerWidth / window.innerHeight;
        this.camera = new THREE.OrthographicCamera(Config.frustumSize * aspect / - 2, Config.frustumSize * aspect / 2, Config.frustumSize / 2, - Config.frustumSize / 2, 0.1, 400)
        this.camera.position.z = 1
        this.scene.add(this.camera)

        // Renderer
        this.renderer = new THREE.WebGLRenderer({ antialias: true });
        this.renderer.setPixelRatio(window.devicePixelRatio);
        container.appendChild(this.renderer.domElement);

        // create an AudioListener and add it to the camera
        this.listener = new THREE.AudioListener();
        this.camera.add(this.listener);

        this.backgroundSound = new THREE.Audio(this.listener);

        this.sdk = new SDK();

        localStorage.removeItem("progressbar_level1");
        localStorage.removeItem("progressbar_level2");
        localStorage.removeItem("progressbar_level3");
        localStorage.removeItem("progressbar_level4");

        localStorage.setItem("background_music", "1");

        this.levelNum = 0;
        this.pause = false;

        // Load Level
        this.loadLevel();

        this.updateScale();
        localStorage.removeItem('presentStep');
        localStorage.removeItem('skipClicked');
        document.addEventListener(visibilityChange, this.handleVisibilityChange, false);

        if (Config.debug) {
            // window.addEventListener('dblclick', () => {
            //     const fullscreenElement = document.fullscreenElement || document.webkitFullscreenElement

            //     if (!fullscreenElement) {
            //         if (this.renderer.domElement.requestFullscreen) {
            //             this.renderer.domElement.requestFullscreen()
            //         }
            //         else if (this.renderer.domElement.webkitRequestFullscreen) {
            //             this.renderer.domElement.webkitRequestFullscreen()
            //         }
            //     }
            //     else {
            //         if (document.exitFullscreen) {
            //             document.exitFullscreen()
            //         }
            //         else if (document.webkitExitFullscreen) {
            //             document.webkitExitFullscreen()
            //         }
            //     }
            // })
        }

        // Animate
        const clock = new THREE.Clock()
        const tick = () => {
            if (this.pause == false) {
                // Update objects
                this.level.update();
            }
            // Render
            this.renderer.render(this.scene, this.camera)

            // Call tick again on the next frame
            requestAnimationFrame(tick)
        }

        tick()
    }

    dispose() {

    }

    handleVisibilityChange = () => {
        console.log("handleVisibilityChange");
        if (document[hidden]) {
            this.pause = true;
            this.backgroundSound.pause();
            this.level.pause(true);
        } else {            
            this.pause = false;
            let val = localStorage.getItem("background_music");
            val = val == null ? 1 : parseInt(val);
            if(val == 1){    
                this.backgroundSound.play();
            }

            this.level.pause(false);
        }
    }

    loadLevel = () => {
        switch (this.levelNum) {
            case 0:
                this.level = new SSScene(0, scene0json, this.loadingManager.obj, this.scene, this.camera, this.renderer, this.backgroundSound, this, this.sdk);
                break;
            case 1:
                this.level = new SSScene(1, scene1json, this.loadingManager.obj, this.scene, this.camera, this.renderer, this.backgroundSound, this, this.sdk);
                break;
            case 2:
                this.level = new SSScene(2, scene2json, this.loadingManager.obj, this.scene, this.camera, this.renderer, this.backgroundSound, this, this.sdk);
                break;
            case 3:
                this.level = new SSScene(3, scene3json, this.loadingManager.obj, this.scene, this.camera, this.renderer, this.backgroundSound, this, this.sdk);
                break;
            case 4:
                this.level = new SSScene(4, scene4json, this.loadingManager.obj, this.scene, this.camera, this.renderer, this.backgroundSound, this, this.sdk);
                break;
        }
    }

    playAgain = () => {
        this.level.dispose();

        this.loadLevel();
    }

    playNextLevel = () => {
        let tempLevelNum = -1;
        localStorage.setItem("previous_level_action_from", tempLevelNum.toString());

        this.level.dispose();

        switch (this.levelNum) {
            case -1:
                this.levelNum = 0;
                break;
            case 0:
                this.levelNum = 1;
                break;
            case 1:
                this.levelNum = 2;
                break;
            case 2:
                this.levelNum = 3;
                break;
            case 3:
                this.levelNum = 4;
                break;
        }

        this.loadLevel();
    }

    playNextGame = () => {

    }

    playPreviousLevel = () => {
        this.level.dispose();

        switch (this.levelNum) {
            case 0:
                this.levelNum = 0;
                break;
            case 1:
                this.levelNum = 0;
                break;
            case 2:
                this.levelNum = 1;
                break;
            case 3:
                this.levelNum = 2;
                break;
            case 4:
                this.levelNum = 3;
                break;
        }

        this.loadLevel();
    }

    updateScale = () => {
        var aspect = window.innerWidth / window.innerHeight;

        this.camera.left = Config.frustumSize * aspect / - 2;
        this.camera.right = Config.frustumSize * aspect / 2;
        this.camera.top = Config.frustumSize / 2;
        this.camera.bottom = - Config.frustumSize / 2;

        this.camera.updateProjectionMatrix();
        this.renderer.setSize(window.innerWidth, window.innerHeight);

        if (this.level)
            this.level.updateForResize();
    }

}

export default Game